import { Directive, ElementRef, HostListener, Input } from '@angular/core';

@Directive({
  selector: '[defaultImg]',
  standalone: true
})
export class DefaultImgDirective{

  @Input() defaultLink:string = 'http://adminpaxzu-devcons.oikos.com.co/images/website/Image_not_available.png';

  constructor(private elementRef: ElementRef) { }

  @HostListener('error')
  loadDefaultImage(){
    const image:HTMLImageElement = this.elementRef.nativeElement
    if(image.src === this.defaultLink){
      console.error(`La imagen por default ${this.defaultLink} est rota, por favor asigna una url vlida`)
    } else {
      image.src = this.defaultLink;
    }
  }

}
